___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Roboto-400";
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
}

h1, h2, h3, h4, h5, h6, a, ul, li {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
}

a:visited {
    color: inherit;
}

button {
    font-family: "Roboto-300";
    appearance: button;
    background-color: var(--blue-600);
    border: 1px solid var(--blue-600);
    border-radius: 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    line-height: 1.15;
    overflow: visible;
    padding: 8px 12px;
    position: relative;
    text-align: center;
    text-transform: none;
    transition: all 80ms ease-in-out;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: max-content;
}

button:focus {
    outline: 0;
}

button:hover {
    background-color: #0A46E4;
    border-color: #0A46E4;
}

button:active {
    background-color: #0039D7;
    border-color: #0039D7;
}`, "",{"version":3,"sources":["webpack://./src/styles/index.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;IACT,UAAU;IACV,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,SAAS;IACT,UAAU;IACV,qBAAqB;IACrB,gBAAgB;AACpB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,iCAAiC;IACjC,iCAAiC;IACjC,kBAAkB;IAClB,sBAAsB;IACtB,cAAc;IACd,eAAe;IACf,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;IAClB,oBAAoB;IACpB,gCAAgC;IAChC,iBAAiB;IACjB,yBAAyB;IACzB,0BAA0B;IAC1B,kBAAkB;AACtB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;AACzB","sourcesContent":["* {\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    margin: 0;\r\n    padding: 0;\r\n    font-family: \"Roboto-400\";\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    height: 100vh;\r\n}\r\n\r\nh1, h2, h3, h4, h5, h6, a, ul, li {\r\n    margin: 0;\r\n    padding: 0;\r\n    text-decoration: none;\r\n    list-style: none;\r\n}\r\n\r\na:visited {\r\n    color: inherit;\r\n}\r\n\r\nbutton {\r\n    font-family: \"Roboto-300\";\r\n    appearance: button;\r\n    background-color: var(--blue-600);\r\n    border: 1px solid var(--blue-600);\r\n    border-radius: 4px;\r\n    box-sizing: border-box;\r\n    color: #FFFFFF;\r\n    cursor: pointer;\r\n    line-height: 1.15;\r\n    overflow: visible;\r\n    padding: 8px 12px;\r\n    position: relative;\r\n    text-align: center;\r\n    text-transform: none;\r\n    transition: all 80ms ease-in-out;\r\n    user-select: none;\r\n    -webkit-user-select: none;\r\n    touch-action: manipulation;\r\n    width: max-content;\r\n}\r\n\r\nbutton:focus {\r\n    outline: 0;\r\n}\r\n\r\nbutton:hover {\r\n    background-color: #0A46E4;\r\n    border-color: #0A46E4;\r\n}\r\n\r\nbutton:active {\r\n    background-color: #0039D7;\r\n    border-color: #0039D7;\r\n}"],"sourceRoot":""}]);
