___CSS_LOADER_EXPORT___.push([module.id, `form#search-weather-form {
    box-sizing: content-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 8px;
    width: 60vw;
    min-width: 200px;
    /* max-width: 500px; */
    padding: 16px;
    border-radius: 4px;
    background-color: var(--slate-400);
    border: 4px solid var(--slate-500);
}

form#search-weather-form input {
    border: none;
    width: 30vw;
    min-width: 150px;
    height: 5vh;
    min-height: 18px;
    border-radius: 4px;
}

form#search-weather-form input:focus-visible {
    outline: none;
    background-color: var(--slate-200);
}

@media screen and (max-width: 375px) {
    form#search-weather-form {
        width: 80vw;
    }

    form#search-weather-form input {
        width: 50vw;
    }
}

@media screen and (min-width:376px) and (max-width:500px) {
    form#search-weather-form {
        width: 80vw;
    }

    form#search-weather-form input {
        width: 80vw;
    }
}

@media screen and (min-width:501px) and (max-width:850px) {
    form#search-weather-form {
        width: 80vw;
    }

    form#search-weather-form input {
        width: 80vw;
    }
}

@media screen and (min-width:851px) {
    form#search-weather-form {
        flex-direction: column;
    }

    form#search-weather-form button {
        font-size: 2.5rem;
        width: 30vw;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/searchWeatherForm.css"],"names":[],"mappings":"AAAA;IACI,uBAAuB;IACvB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,QAAQ;IACR,WAAW;IACX,gBAAgB;IAChB,sBAAsB;IACtB,aAAa;IACb,kBAAkB;IAClB,kCAAkC;IAClC,kCAAkC;AACtC;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,WAAW;IACX,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,kCAAkC;AACtC;;AAEA;IACI;QACI,WAAW;IACf;;IAEA;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,WAAW;IACf;;IAEA;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,WAAW;IACf;;IAEA;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,sBAAsB;IAC1B;;IAEA;QACI,iBAAiB;QACjB,WAAW;IACf;AACJ","sourcesContent":["form#search-weather-form {\r\n    box-sizing: content-box;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n    gap: 8px;\r\n    width: 60vw;\r\n    min-width: 200px;\r\n    /* max-width: 500px; */\r\n    padding: 16px;\r\n    border-radius: 4px;\r\n    background-color: var(--slate-400);\r\n    border: 4px solid var(--slate-500);\r\n}\r\n\r\nform#search-weather-form input {\r\n    border: none;\r\n    width: 30vw;\r\n    min-width: 150px;\r\n    height: 5vh;\r\n    min-height: 18px;\r\n    border-radius: 4px;\r\n}\r\n\r\nform#search-weather-form input:focus-visible {\r\n    outline: none;\r\n    background-color: var(--slate-200);\r\n}\r\n\r\n@media screen and (max-width: 375px) {\r\n    form#search-weather-form {\r\n        width: 80vw;\r\n    }\r\n\r\n    form#search-weather-form input {\r\n        width: 50vw;\r\n    }\r\n}\r\n\r\n@media screen and (min-width:376px) and (max-width:500px) {\r\n    form#search-weather-form {\r\n        width: 80vw;\r\n    }\r\n\r\n    form#search-weather-form input {\r\n        width: 80vw;\r\n    }\r\n}\r\n\r\n@media screen and (min-width:501px) and (max-width:850px) {\r\n    form#search-weather-form {\r\n        width: 80vw;\r\n    }\r\n\r\n    form#search-weather-form input {\r\n        width: 80vw;\r\n    }\r\n}\r\n\r\n@media screen and (min-widt