___CSS_LOADER_EXPORT___.push([module.id, `div#main-container {
    height: 100%;
    width: 100%;
    display: flex;
    gap: 16px;
    padding-top: 8px;
    padding-bottom: 8px;

    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    box-shadow: rgba(0,0,0,0.45) 0 20px 10px -20px inset,
                rgba(0,0,0,0.45) 0 -20px 10px -20px  inset;
}

div.hidden {
    visibility: hidden;
    height: 0px;
}

div#weather-display-container {
    box-sizing: content-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60vw;
    /* max-width: 500px; */
    min-width: 200px;
    gap: 8px;
    padding: 16px;
    margin-left: 16px;
    margin-right: 16px;
    border-radius: 4px;
    background-color: var(--slate-400);
    border: 4px solid var(--slate-500);
}

div#location-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    width: 90%;
    gap: 8px;
    padding: 8px;
    border-radius: 4px;
    font-family: "Roboto-400";
    /* font-size: 2rem; */
    flex-wrap: wrap;
    background-color: var(--slate-300);
}

div#city-display {
    font-size: 1.8rem;
    font-family: "Roboto-500";
}

div#province-display {
    font-size: 1.5rem;
    font-family: "Roboto-300";
}

div#country-display {
    font-size: 1.5rem;
    font-family: "Roboto-400";
}

div#temperature-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 16px;
    padding: 8px;
    width: 90%;
    /* padding: 8px; */
    border-radius: 4px;
    background-color: var(--slate-300);
    flex-wrap: nowrap;
}

div#temperature-display {
    font-size: 4rem;
    font-family: "Roboto-400";
}

div#current-condition-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.6rem;
    gap: 16px;
}

div#current-condition-container > div {
    width: fit-content;
}

div#wind-condition-container {
    display: flex;
    justify-content: center;
    font-family: "Roboto-300";
    gap: 8px;
    font-size: 1.3rem;
    border-radius: 4px;
    width: 90%;
    padding: 8px;
    background-color: var(--slate-300);
}

@media screen and (max-width: 375px) {
    div#weather-display-container {
        width: 80vw;
    }

    div#city-display {
        font-size: 2rem;
    }

    div#province-display {
        visibility: hidden;
        height: 0px;
    }

    div#country-display {
        font-size: 1.2rem;
    }

    img#current-condition-icon {
        width: 6rem;
    }

    div#temperature-container {
        flex-direction: column;
    }
}

@media screen and (min-width:376px) and (max-width:500px) {
    div#weather-display-container {
        width: 80vw;
    }

    div#location-container {
        font-size: 1rem;
    }

    div#province-display {
        font-size: 1rem;
    }

    div#country-display {
        font-size: 1rem;
    }

    div#temperature-container {
        gap: 8px;
        font-size: 1rem;
        /* justify-content: space-between; */
    }

    div#current-condition-display {
        font-size: 1rem;
    }

    div#temperature-display {
        font-size: 2rem;
    }

    div#current-condition-container {
        font-size: 1.3rem;
    }

    div#wind-condition-container {
        font-size: 1rem;
    }
}

@media screen and (min-width:501px) and (max-width:850px) {
    div#weather-display-container {
        width: 80vw;
    }

    div#city-display {
        font-size: 2.5rem;
    }

    div#province-display {
        font-size: 1.5rem;
    }

    div#country-display {
        font-size: 1.5rem;
    }

    div#temperature-container {
        /* gap: 8px; */
        /* font-size: 1rem; */
        /* justify-content: space-between; */
    }

    div#current-condition-display {
        font-size: 1rem;
    }

    div#temperature-display {
        font-size: 3rem;
    }

    div#current-condition-container {
        flex-direction: row;
    }

    div#current-condition-display {
        font-size: 1.5rem;
    }

    div#wind-condition-container {
        font-size: 1rem;
    }
}

